/* Display */
.block,
.d-block {
	display: block;
}

.inline,
.d-inline {
	display: inline;
}

.inline-block,
.d-inline-block {
	display: inline-block;
}

.inline-flex,
.d-inline-flex {
	display: inline-flex;
}

.flex,
.d-flex {
	display: flex;
}

.grid,
.d-grid {
	display: grid;
}

.d-none {
	display: none;
}

